export default {
    props: {
        size: {     //
            type: Number,
            default: 3072
        },
        disabled: {  //
            type: Boolean
        },
        iconSize: {   //icon
            type: Number,
            default: 24
        },
        name: {      //input
            type: String,
            default: 'file'
        },
        accept: {   //
            type: Array,
            default() {
                return [];
            }
        },
        acceptErrorMessage: {  //
            type: String,
            default: ''
        },
        compress: {         //
            type: Boolean,
            default: true,
        },
        compressSize: {      //
            type: Number,
            default: 512,
        },
        data: {              //
            type: Object,
            default() {
                return {};
            },
        },
        action: {           //
            type: String,
            default: '',
        },
        headers: {         //
            type: Object,
            default() {
                return {};
            },
        },
        showLabel: {    //
            type: String
        },
        response: {    //
            type: String
        },
        imgWidth: {    //
            type: [Number, Boolean],
            default: 800,
        },
        quality: {    //
            type: Number,
            default: 1,
        },
        beforeUpload: {  //
            type: Function
        },
        onSuccess: {     //
            type: Function
        },
        onError: {       //
            type: Function
        },
        onLoadend: {     //
            type: Function
        },
        onProgress: {    //
            type: Function
        },
        onSuccessText: {   //
            type: String,
            default: ''
        },
        onErrorText: {    //
            type: String,
            default: ''
        },
        beforeRemove: {    //
            type: Function
        },
        showRemove: {     //icon
            type: Boolean,
            default: true
        },
        type: {           //
            type: String,
            default: 'single',
            validator: function (value) {
                return ["single", "multiple"].includes(value);
            }
        },
        maxNumber: {    //
            type: Number
        },
        isImage: {      //
            type: Boolean,
            default: true
        }
    }
}


